export let el = {
    setup: {},
    update: {},
};
export class ElementWrapper {
    constructor(el) {
        this.el = document.getElementById(el);
    }
    setText(text) {
        if (this.el)
            this.el.textContent = text;
    }
    static setText(el, text) {
        new ElementWrapper(el).setText(text);
    }
    setHTML(html) {
        if (this.el)
            this.el.innerHTML = html;
    }
    static setHTML(el, text) {
        new ElementWrapper(el).setHTML(text);
    }
    onClick(func) {
        if (this.el)
            this.el.onclick = func;
    }
    static onClick(el, func) {
        new ElementWrapper(el).onClick(func);
    }
}
export function updateHTML() {
    for (let x in el.update)
        el.update[x]();
}
